<template>
    <div>
        <!-- 头部开始 -->
        <div class="header">
            <van-nav-bar
                title="确认订单"
                left-arrow
                @click-left="onClickLeft"
            />
        </div>
        <!-- 头部结束 -->

        <!-- 地址栏 -->
        <div class="address">
             <van-contact-card 
                :type="cardType" 
                :name="name" 
                :tel="tel" 
                add-text="添加联系人"
                @click="addressed"
                :editable="true" 
            />
        </div>
        <!-- 地址栏结束 -->

        <!-- 商品信息 -->
        <div class="shopcontent">
            <div class="topnav">
                <van-row>
                    <van-col span="3">
                        <img src="../../assets/images/mi.png" alt="">
                    </van-col>
                    <van-col span="21">
                        <h1>小米官方旗舰店</h1>
                    </van-col>
                </van-row>    
            </div>
            <!-- 商品描述 -->
            <div class="shoplist" v-for="(item,index) in lists " :key="index">
                <van-row>
                    <van-col span="8">
                        <van-image
                            height="100px"
                            fit="contain"
                            :src="item.imgarr"
                        />
                    </van-col>
                    <van-col span="14">
                        <div class="content">
                            <h1>{{item.name}} </h1>
                            <!-- <h1>小米cc9pro手机 1亿像素 10倍混合光学变焦 </h1> -->
                            <h2>魔法绿境 6G+128G 全网通</h2>
                            <div class="price">
                                <span class="money">￥{{item.price}}</span>
                                <span class="num">数量：{{item.num}}</span>
                            </div>
                        </div>
                        <div class="bottom">
                            <p><span ><van-icon name="cash-back-record" class="icon"/></span> 支持七天无理由退货(激活不支持)</p>
                            <p><span ><van-icon name="logistics" class="icon"/></span> 支持京东物流 <span ><van-icon name="info-o"  class="icon"/></span> 价格说明</p>
                        </div>
                    </van-col>
                </van-row>
            </div>
          </div>
        <!-- 商品信息结束 -->
        <!-- 服务 -->
        <div class="serve">
            <div class="top">
                <van-row>
                    <van-col span="18">
                        <p class="t1">配送服务</p>
                        <p class="t2">中小件送货时间</p>
                    </van-col>
                    <van-col span="6">
                        <p class="title">京东快递</p>
                        <p class="t2">9.00-21.00</p>
                    </van-col>
                </van-row>
             </div>
                <van-divider />
            <div class="center">
                <p class="t1">退换无忧 <span class="t2">退换货课获得一次运费赔付<van-icon class="icon" name="info-o" /></span></p>
            </div>
                <van-divider />
            <div class="bottom">
               <van-row>
                    <van-col span="18"><p class="t1">店铺备注</p></van-col>
                    <van-col span="6"><p class="t2">选填，给商家留言</p></van-col>
                </van-row>
            </div> 
        </div>

        <!-- 发票优惠 -->
        <div class="bottom">
            <van-cell title="发票信息" is-link value="扫码查看"  @click="showPopup" />
            <van-popup v-model="show">
                <img src="../../assets/images/二维码.png" alt="">
            </van-popup>
            <van-cell title="优惠券" is-link value="无可用" />
            <van-cell title="礼品ka" is-link value="无可用" />
            <van-cell title="红包" is-link value="无可用" />
        </div>

        <!-- 付款
        <div class="pay">
            <p>
               <span class="des">商品金额</span>
               <span class="price">￥2498.00</span>
            </p>
             <p>
               <span class="des">运费</span>
               <span class="price">+￥0.00</span>
            </p>
           <p>
                <span class="prices">￥2498.00</span>
                <span class="text">实付金额：</span>
            </p>
            <van-button round type="info" block>在线支付</van-button>
        </div> -->

        <van-divider>商品不支持货到付款</van-divider>
            
       <van-submit-bar 
        :price="total*100" 
        button-text="提交订单" @submit="onsubmit" />



    </div>
</template>

<script>
    export default {
        data(){
            return {
                name:"",
                tel:"",
                show: false,
                
            }
        },
        methods: {
            showPopup() {
                this.show = true;
            },
            
            // 总价格计算
            sum(){
                this.total = this.lists.reduce((total,item)=>{
                    // 判断选项选中计算价格
                    if(!item.isSelect) return total; //返回默认价格
                    return total + parseInt(item.price) * parseInt(item.num); //总价格
                },0) 
            },
        
            onClickLeft() {
                 // 保存订单数据
                let order = {
                    orderId:new Date().getTime(),//作为订单号
                    address:this.address,
                    shopist:this.lists,
                    dateTime:new Date().toLocaleString(),//订单时间
                    type:1 //订单的状态
                }
                // 调用vuex订单数据状态
                this.$store.commit("order/add",order);
                // 清空购物车
                this.$store.state.cart.lists = [];
                this.$store.state.cart.num = 0;
                this.$router.push('/cart');
                this.$store.state.botnav = true;
            },
            // 定义跳转方法
            addressed(){
                // 如果地址没有数据，直接跳转地址添加页面
                if(this.$store.state.address.lists.length==0){
                    this.$router.push('/addressadd')       
                }else{
                    this.$router.push('/address')
                }
            },

            // 提交数据的方法
            onsubmit(){
                // 跳到订单列表
                this.$router.push("/orderlist/orderget") 
                // 清空购物车
                this.$store.state.cart.lists = [];
                this.$store.state.cart.num = 0;

                // 定义一个保存订单数据
                let order = {
                    orderId:new Date().getTime(),//作为订单号
                    address:this.address,
                    shopist:this.lists,
                    dateTime:new Date().toLocaleString(),//订单时间
                    type:2 //订单的状态
                }
                // 调用vuex订单数据
                this.$store.commit("order/add",order);
        
            }
           
        },
        computed:{
             cardType(){
                return this.$store.state.address.lists.length == 0 ? "add":"edit"
            }
        },
          created(){
            //   获取地址的数据
            let data = this. $store.state.address.lists.find(item=>item.isDefault)
             if(data){
                //  名字
                this.name = data.name;
                // 电话
                this.tel = data.tel;
                //定义地址对象
                this.address = data; 
            }
            
            this.lists = this.$store.state.cart.lists;
            this.sum();

            
        },


       
    }
</script>

<style lang="scss" scoped>
.shopcontent{
    margin-top: 15px;
    border-radius: 20px;
    background-color: white;
    .topnav{
        height: 30px;
        padding: 0 15px;
        border-bottom: 1px solid #f2f2f2;
        img{
            width: 20px;
            display: inline-block;
            vertical-align: middle;
        }
        h1{
            font-size: 16px;
            // line-height: 30px;
        }
    }
    .shoplist{
        // margin-bottom: 12px;
         border-bottom: 1px solid #f2f2f2;
        background-color: white;
        padding: 8px 10px;
        padding-top: 8px;

        position: relative;
        .van-checkbox{
            position: relative;
            left:4px;
            top:34px;
        }
        .content{
            padding-left: 8px;
            h1{
                font-size: 14px;
            }
            h2{
                font-size: 12px;
                font-weight: normal;
                margin-bottom: 3px;
                color: #a0a2a3;
            }
            .price{
                 margin-top: 3px;
                height: 30px;
                .money{
                    float: left;
                    font-size: 16px;
                    color: red;
                }
                .num{
                    margin-top: 5px;
                    float: right;
                    color: #a0a2a3;
                }  
            } 
        }
        .bottom{
            margin-left: 10px;
            p{
                width: 200px;
                color: #a0a2a3;
                font-size: 10px;
                .icon{
                    color: #55a0fb;
                    line-height: 18px;
                }
            }
        }
    }
}
// 服务
.serve{
    padding: 0 15px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    margin-bottom: 15px;
    background-color: #fff;
    .top,
    .center,
    .bottom{
        .t1{
            font-size: 18px;
            color: #333333;
        }
        .title{
            font-size:14px ;
            color: #333333
        }
        .t2{
            color: #999999;
            font-size: 12px;
            .icon{
                vertical-align: middle;
                margin-left: 5px;

            }
        }
    }
    .top{
        box-sizing: border-box;
        padding-top: 12px;
    }
}

// bottom
.bottom{
    border-radius: 20px;
    background-color: white;
    margin-bottom: 15px;
}

// 付款
.pay{
       box-sizing: border-box;
       padding-top: 5px;
       background-color: white;
   p{   
       height: 40px;
       padding: 0 15px;
       background-color: white;
     .des{
         float: left;
         font-size: 18px;
     }
     .price{
         float: right;
         color: red;
         font-size: 14px;
     }
     .prices,
     .text{
         float: right;
     }  
     .text{
         font-size: 20px;
         font-weight: bold;
     }
     .prices{
         color: red;
         font-size: 16px;
         line-height: 40px;
     }
   }
}

</style>